<template>
    <div class="all-container ChargingLineAnalysis" style="height:100%">
        <div class="tabs-show tabs-shows" style="margin:0.8em 0 0 0">
            <template v-for="item in formList">
                <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom"></inputs>
                <datePicker ref="timer" style="margin-left:10px" v-if="item.type == 'timer'"    type= "doubleDate"  :dateOpt="item.options"
                            @getDatePicker="getFrom"></datePicker>
            </template>
        </div>
        <div style="height:18em;background: transparent;width: 100%">
            <div class="lineCharts1" ref="lineCharts" v-if="showLine">
                <div  class="emptyShowPic">
                    <div class="emptyContent">
                        <img src="dist/img/empty-data.png">
                        <p>查不到相关数据</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="" style="font-size: 0.94em;height: calc(100% - 20em);background: transparent;margin-top:0.5em">
            <div ref="myOwnTable" class="table-container" style="height:calc(100% )">
                <tables ref="myTable" :tableOpt="tableOpt" class="tablePadding" v-if="tableShow" @rowDbClick="openDialogs"></tables>
            </div>
        </div>
        <dialogs type="alert" ref="alerts"></dialogs>
        <dialogs type="customer" ref="detail" width="65em" maxHeight="620px">
            <div slot="header">
                <span>{{title}}</span>
            </div>
            <div slot="content">
                <div class="costAccount-content" v-if="isDialogTable">
                    <div class="loading" v-show="loadings">
                        <img src="dist/img/loading.gif">
                        <p>正在加载，请稍后...</p>
                    </div>
                    <div class="mychar-box">
                        <div ref="currentChart" class="mychar">
                            <div  class="emptyShowPic">
                                <div class="emptyContent">
                                    <img src="dist/img/empty-data.png">
                                    <p>查不到相关数据</p>
                                </div>
                            </div>
                        </div>
                        <div ref="voltageChart">
                            <div  class="emptyShowPic">
                                <div class="emptyContent">
                                    <img src="dist/img/empty-data.png">
                                    <p>查不到相关数据</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <tables ref="caution"  class="dialogTable" :tableOpt="dialogTable"></tables>
                </div>
            </div>
        </dialogs>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                resultShow:false,
                searchData: {
                    deviceType: "07",
                    startDate: "",
                    endDate: "",
                    deviceNo: ""
                },
                showLine: true, //柱状图无数据显示
                line: '', //折线图dom
                tableShow: false, //table延迟加载
                loadings: true,
                controls: '',
                tableOpt: {
                    column: [
                        {title: '日期', field: 'date',textAlign: 'center',width: '120'},
                        {title: '总表表号', field: 'deviceNo', textAlign: 'center', width: '100'},
                        {title: '总表起度', field: 'lastReadValue', textAlign: 'center', width: '100'},
                        {title: '总表止度', field: 'readValue', textAlign: 'center', width: '100'},
                        {title: '电表电量（KW·h）', field: 'meterElect', textAlign: 'center', width: '100'},
                        {title: '设备电量（KW·h）', field: 'deviceElect', textAlign: 'center', width: '100'},
                        {title: '损耗电量（KW·h）', field: 'lossElect', textAlign: 'center', width: '100',sortable: true},
                        {title: '损耗率（%）', field: 'lossRate', textAlign: 'center', width: '100',sortable: true},
                        {title: '充电次数', field: 'chargeTime', textAlign: 'center', width: '80'},
                        {title: '位置', field: 'installAddr', textAlign: 'center', width: '100'},
                    ],
                    url: 'charger/totalLineLoss',
                    method: 'post',
                    // method: 'get',
                    // url: 'dist/json/ChargingLineAnalysis.json',
                    dataRow: "list",
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20, 40, 60],
                    queryParam: {},
                    isExport: true,
                    exportURL: 'charger/totalLineLossDownload',
                    totalOpt: [
                        {title: '电表电量', field: 'meterElect', textAlign: 'center', width: '100'},
                        {title: '设备电量', field: 'deviceElect', textAlign: 'center', width: '100'},
                        {title: '损耗电量', field: 'lossElect', textAlign: 'center', width: '100'},
                        {title: '损耗率', field: 'lossRate', textAlign: 'center', width: '100'}
                    ],
                    showTotal: true,
                },
                // 以下为弹框
                lineData: {},  // 上方图
                tableData: [], // 下发表格
                lineV: '',
                lineA: '',
                isDialogTable: false,
                title: '',
                dialogTable:{
                    column: [
                        {title: '设备编号', width: '50', field: 'deviceNo', textAlign: 'center'},
                        {title: '端口号', width: '50', field: 'port', textAlign: 'center',  },
                        {title: '电量(KW·h)', width: '100', field: 'deviceElect', textAlign: 'center',sortable: true},
                        {title: '状态', field: 'state',textAlign: 'center',width: '100', type: 'string',
                            formatter: function (val) {
                                switch(val) {
                                    case '充电中':
                                        return '<div class="red">充电中</div>'
                                    case '充电完成':
                                        return '<div class="green">充电完成</div>'
                                    default:
                                        return '<div >--</div>'
                                }
                            }},
                        {title: '充电时长(分钟)', width: '100', field: 'userTime', textAlign: 'center'},
                        {title: '开始时间', width: '120', field: 'startDate', textAlign: 'center'},
                        {title: '结束时间', width: '120', field: 'endDate', textAlign: 'center'},
                        {title: '最大功率(W)', width: '100', field: 'mostPower', textAlign: 'center'},
                        {title: '最大电流(A)', width: '100', field: 'mostCurrent', textAlign: 'center'}
                    ],
                    queryParam: {},
                    pageSize: 100,
                    pageNumber: 1,
                    height: 290,
                    noExportCurrent: true,
                    noPagination: true,
                    isExport: true,
                    sortOrder: "",
                    exportURL: 'charger/dayLineLossDownload',
                }
            }
        },
        computed: {
            formList: function () {
                let dates = this.initDate(true);
                return [
                    {
                        type: 'timer',
                        options: {
                            key: 'searchDate',
                            value: {startDate: dates.start, endDate: dates.end},
                            width: '23em',
                            fontSize: '0.8em'
                        }
                    },
                    {
                        type: 'input',
                        options: {
                            type: 'typeSearch',
                            key: 'searchData',
                            selectOpt: {
                                width: '5em',
                                cs: 'client',
                                data: [
                                    {text: '表号', value: 'deviceNo'},
                                ]
                            }
                        }
                    }
                ]
            },
        },
        watch: {
            'dialogTable.sortOrder': {
                handler(val, oldVal) {
                    this.orderedDeviceElect(val)
                },
                deep: true
            },
        },
        methods: {
            orderedDeviceElect: function (val) {
                let tableData = [];
                if(val === 'asc'){
                    tableData = this.tableData.sort(function(x, y){
                        return x.deviceElect - y.deviceElect;
                    });
                }else if(val === 'desc'){
                    tableData = this.tableData.sort(function(x, y){
                        return y.deviceElect - x.deviceElect;
                    });
                }else{
                    tableData = this.tableData;
                }
                this.$refs.caution.resetTableOpt({data: tableData});
            },
            getFrom: function (val) {
                if(val.combine){
                    if ( this.searchData[val.key.split('.')[1]] !== val.val) {
                        this.getOption(val, true);
                        this.getParams();
                    }
                }else if(val.key === 'searchDate'){
                    let arr = val.val.split('至');
                    this.searchData.startDate = arr[0];
                    this.searchData.endDate = arr[1];
                    this.getParams();
                }
            },
            getParams: function () {
                this.makeLine();
                this.searchTable(this.searchData, 1);
            },
            //左侧柱状图，各时间段的损耗电量
            makeLine: function () {
                let self = this;
                let option = {
                    tooltip : {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross',
                            label: {
                                backgroundColor: '#6a7985'
                            }
                        }
                    },
                    grid: {
                        left: '2.5%',
                        right: '0.5%',
                        bottom: '5%',
                        top: 50,
                        containLabel: true
                    },
                    xAxis : [
                        {
                            type : 'category',
                            boundaryGap : [1,1],
                            data : [],
                            axisLine:{
                                show: true,
                                lineStyle:{
                                    color: '#fff'
                                }
                            },
                            axisLabel:{
                                showMinLabel: true,
                            },
                            axisTick:{
                                show: false
                            },
                        }
                    ],
                    yAxis : [
                        {
                            name: "损耗电量(KW·h)",
                            nameTextStyle: {
                              color: "#fff",
                            },
                            type : 'value',
                            axisLine: {
                                show: false,
                                lineStyle:{
                                    color: '#3C9BF9'
                                }
                            },
                            axisTick: {
                                show: false
                            },
                            splitLine: {
                                show:true,
                                lineStyle: {
                                    color: 'rgba(255,255,255, 0.1)',
                                    width: 2,
                                    type: 'dotted'
                                }
                            },
                            axisLabel: {
                                show: true,
                                formatter:'{value}',
                                textStyle: {
                                    color: '#fff'
                                }
                            },
                        }
                    ],
                    series : [
                        {
                            name:'损耗电量',
                            type:'line',
                            stack: '损耗电量',
                            symbol: 'emptyCircle',
                            areaStyle: {
                                normal: {
                                    color:  new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                        offset: 0,
                                        color: 'rgba(22,90,162, 1)'
                                    }, {
                                        offset: 1,
                                        color: 'rgba(22,90,162, 0.1)'
                                    }])
                                }
                            },
                            markPoint: {
                                // symbol: "pin",
                                data: [
                                    {
                                        type: 'max',
                                        name: '最大值',
                                        symbol: 'image://dist/img/widsomApp/popup_blue.png',
                                        symbolOffset: [0, '-55%'],
                                        label: {
                                            normal: {
                                                padding: [5,10,15,10],
                                                color: '#fff',
                                                position: 'inside'
                                            }

                                        }
                                    }
                                ]
                            },
                            lineStyle:{
                                color: '#217FDF'
                            },
                            itemStyle: {
                                color: '#217FDF',
                                borderWidth: 3
                            },
                            data:[]
                        }
                    ]
                };
                let param = self.searchData;
                param.pageNumber = 1;
                param.pageSize = 10;
                self.getRequest({
                    // url: 'dist/json/ChargingLineAnalysis.json',
                    // method: 'get',
                    url:"charger/totalLineLoss",
                    method: 'post',
                    param: param,
                    success: function (data) {
                        for (let i = 0; i < data.data.loss.length; i++){
                            option.xAxis[0].data.push(data.data.loss[i].date);
                            option.series[0].data.push(data.data.loss[i].lossElect||0);
                        }
                        self.line = echarts.init(self.$refs.lineCharts);
                        self.line.setOption(option, true);
                    }
                })
            },
            // 电压折线图
            getLineC: function () {
                this.lineV  = echarts.init(this.$refs.currentChart);
                this.getLine(this.lineV, ['rgba(192,64,89, 1)', 'rgba(192,64,89, 0.1)'], ['总表电压', 'V'], 'voltageA', 'popup_red')
            },
            //电流折线图
            getLineV: function () {
                this.lineA  = echarts.init(this.$refs.voltageChart);
                this.getLine(this.lineA, ['rgba(184,193,62, 1)', 'rgba(184,193,62, 0.1)'], ['总表电流', 'A'], 'currentA', 'popup_yellow')
            },
            getLine: function (obj, color, typeText, key, img) {
                let option = {
                    toolbox: {
                        show: false,
                        feature: {
                            dataZoom: {
                                yAxisIndex: 'none'
                            },
                            restore: {},
                            saveAsImage: {}
                        }
                    },
                    grid: {
                        left: 40,
                        right: 30,
                        bottom:40,
                        top: 50
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: [],
                        nameTextStyle: {
                            color: 'rgba(255,255,255, 0.1)',
                        },
                        axisLabel: {
                            color: 'rgba(255,255,255, 0.1)',
                            textStyle: {
                                color:'rgba(255,255,255, 1)'
                            }
                        },
                        splitLine: {
                            show:true,
                            lineStyle: {
                                color: 'rgba(255,255,255, 0.1)',
                                type: 'dotted',
                                width: 2
                            }
                        },
                        axisLine: {
                            show:true,
                            showMinLabel: true,
                            lineStyle: {
                                color: 'rgba(255,255,255, 0.1)'

                            }
                        }
                    },
                    yAxis: {
                        name: typeText[0] + ' ( ' + typeText[1] + ' ) ',
                        type: 'value',
                        nameTextStyle: {
                            color: 'rgba(255,255,255, 1)',
                        },
                        axisLabel: {
                            color: 'rgba(255,255,255, 0.1)',
                            textStyle: {
                                color: 'rgba(255,255,255, 1)'
                            }
                        },
                        axisLine: {
                            show:true,
                            lineStyle: {
                                color: 'rgba(255,255,255, 0.1)'
                            }
                        },
                        splitLine: {
                            show:true,
                            lineStyle: {
                                color: 'rgba(255,255,255, 0.1)',
                                width: 2,
                                type: 'dotted'
                            }
                        }
                    },
                    tooltip : {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross'
                        }
                    },
                    series:
                        {
                            name: typeText[0],
                            type:'line',
                            smooth:false,
                            symbol: "circle",
                            symbolSize: 8,
                            // sampling: 'max',
                            itemStyle: {
                                normal: {
                                    color: color[0]
                                }
                            },
                            markPoint: {
                                // symbol: "pin",
                                data: [
                                    {
                                        type: 'max',
                                        name: '最大值',
                                        symbol: 'image://dist/img/widsomApp/'+img+'.png',
                                        symbolOffset: [0, '-55%'],
                                        label: {
                                            normal: {
                                                padding: [5,10,15,10],
                                                color: '#fff',
                                                position: 'inside'
                                            }

                                        }
                                    }
                                ]
                            },
                            areaStyle: {
                                normal: {
                                    color:  new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                        offset: 0,
                                        color: color[0]
                                    }, {
                                        offset: 1,
                                        color: color[1]
                                    }])
                                }
                            },
                            data: []
                        }
                };
                option.xAxis.data = [];
                option.series.data = [];
                for(let i = 0; i < this.lineData.length; i++){
                    option.xAxis.data.unshift(this.lineData[i].date.split(" ")[1])
                    option.series.data.unshift(this.lineData[i][key]||0);
                }
                obj.setOption(option, true);
            },
            getLineData: function (row) {
                let self = this;
                let param = {
                    "deviceNo": row.deviceNo,
                    "deviceType": "07",
                    "date": row.date
                };
                self.getRequest({
                    // url: 'dist/json/ChargingLineAnalysisDialogs.json',
                    // method: 'get',
                    url: 'charger/dayLineLoss',
                    method: 'post',
                    param: param,
                    success: function (data) {
                        let Data = data.data;
                        self.loadings = false;
                        self.lineData = Data.import;
                        self.tableData = Data.list
                        self.dialogTable.queryParam = param;
                        self.dialogTable.sortOrder = "";
                        self.$refs.caution.resetTableOpt({data: self.tableData});
                        self.getLineC();
                        self.getLineV();
                    },
                    error: function(data){
                        self.loadings = false;
                        self.$refs.alerts.show({
                            msg: data.message
                        });
                    }
                })
            },
            openDialogs: function (row) {
                let that = this;
                this.title = '总表表号:'+ row.val.deviceNo +'，时间:' + row.val.date + '，损耗率:' + row.val.lossRate + '%';
                this.$refs.detail.show({
                    hasClose: true,
                    action:{
                        cancel: function () {
                            console.log(that.isDialogTable)
                            that.isDialogTable = false;
                            that.loadings = true;
                        }
                    }
                });
                this.isDialogTable = true;
                this.getLineData(row.val);
            },
            resizeCon: function () {
                let that = this;
                that.line !== '' ? that.line.resize() : '';
                that.lineA !== ''?  that.lineA.resize() : '';
                that.lineV !== ''?  that.lineV.resize() : '';
                if ( that.$refs.myTable) {
                    that.$refs.myTable.onResize();
                }
            },
            windowChange: function () {
                let that = this;
                that.resizeCon();
                // window.onresize = function () {
                //     that.resizeCon();
                // }
            },
            init: function () {
                let that = this;
                let dates = this.initDate(true);
                this.searchData.startDate = dates.start
                this.searchData.endDate = dates.end;
                this.tableOpt.queryParam = this.searchData;
                this.tableOpt.height = this.$refs.myOwnTable.clientHeight - 60;
                this.tableOpt.isExport = this.controls[6];
                this.dialogTable.isExport = this.controls[6];
                that.tableShow = true;
                this.makeLine();
                // window.onresize = function () {
                //     that.windowChange();
                // }
            }
        },
        mounted:function(){
            this.isIn();
            this.getAction();
        },
        created: function () {

        }
    }
</script>
<style lang="scss">
    $headerColor: #44BBDD;
    .ChargingLineAnalysis{
        .info-container1 {
            margin-top: 5px
        }

        .lineCharts1 {
            width: calc(100% - 2px);
            height: 14.5em;
            float: left;
        }
        .tabs-shows {
            margin: 0.8em 0 0 0;
            float: right;
            .date-picker{
                .date-button{
                    line-height:1.9;
                    .icon-date{
                        margin-top:-0.1em!important
                    }
                }
            }
        }
        .costAccount-content{
            position: relative;
            width: 100%;
            .mychar-box{
                width: 100%;
                height: 15em;
                display: flex;
                &>div{
                    height: 15em;
                    width: 50%;
                }
            }
            .loading{
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: calc(100% - 2px);
                background: rgba(2, 22, 69, 1);
                z-index: 10;
                p{
                    position: absolute;
                    top: 50%;
                    left: calc(50% - 4em);
                }
                img{
                    width: 4em;
                    height: 4em;
                    position: absolute;
                    top: calc(50% - 4em);
                    left: calc(50% - 2em);
                }
            }
        }
    }

</style>



